@import "../../global.scss";

.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
    }
    .container{
        width: 90%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        // flex: 1 1 1 1 1 1;
        // flex: 0 0 0 0 0 33.333333%;

        .item{
            width: 330px;
            height: 200px;
            border-radius: 20px;
            border: 1px solid rgba(211, 211, 211, 0.918);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            h3{
               position: absolute;
               font-size: 20px; 
            }

            img{
                width: 100%;
                height: 100%;
                object-fit:contain;
                z-index: 1;
                display: flex;
            }

            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}